<template>
  <div class="wrapper">
      <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
  <el-breadcrumb-item :to="{ path: '/home/wellcom' }">首页</el-breadcrumb-item>
  <el-breadcrumb-item>用户管理</el-breadcrumb-item>
  <el-breadcrumb-item>用户列表</el-breadcrumb-item>
</el-breadcrumb>
<!-- 卡片视图  -->
<el-card>
    <!-- 添加角色 -->
   <el-row>
      <el-col>
  <el-button type="primary">添加角色</el-button>
      </el-col>
   </el-row>
   <!-- 角色列表区域 -->
     <el-table :data="roleslist" border stripe >
            <el-table-column  type="expand" >
               <template slot-scope="scope">
                  <el-row :class="['bottom-bor',index===0?'top-bor':'']" v-for="(item,index) in scope.row.children" :key="index">
                      <!-- 渲染一级 -->
                      <el-col>
                       <el-tag>
                           {{item.authName}}
                       </el-tag>
                      </el-col >
                      <!-- 渲染二级跟三级 -->
                      <el-col v-for="(items) in item.children" :key="items.id">
                          <!-- 渲染二级 -->
                         <el-row>
                      <el-col>
                        <el-tag type="success">
                            {{items.authName}}
                        </el-tag>   
                      </el-col>
                       <!-- 渲染三级 -->
                       <el-col >
                        <el-tag v-for="(itemss) in items.children" :key="itemss.id" type="warning">
                            {{itemss.authName}}
                        </el-tag>
                      </el-col>
                      </el-row>


                      </el-col>
                  </el-row>
               </template>

            </el-table-column>
        <el-table-column    type="index" label="#"></el-table-column>
        <el-table-column    label="角色名称" prop="roleName"></el-table-column>
        <el-table-column    label="角色描述" prop="roleDesc"></el-table-column>
        <el-table-column    label="操作"  width="300px">
            <template :slot-scope="scope">
                  <el-button type="primary" icon="el-icon-search" size='mini'>编辑</el-button>
                  <el-button type="danger" icon="el-icon-search" size='mini'>删除</el-button>
                  <el-button type="warning" icon="el-icon-search" size='mini'>分配权限</el-button>
            </template>
        </el-table-column>
    </el-table>

</el-card>
  </div>
</template>

<script>
export default {
  components: {},
  props: {},
  data() {
    return {
        roleslist:[]
    };
  },
  watch: {},
  computed: {},
  methods: {
    async  getRoles(){
         
    let {data:res}=await this.$http.get('roles')
    if(res.meta.status!==200){
          this.$message.error(res.meta.msg)
    }else{
            this.$message.success(res.meta.msg)
    }
      this.roleslist=res.data
      console.log(this.roleslist)
      }
  },
  created() {
      this.getRoles()
  },
  mounted() {}
};
</script>
<style lang="less" scoped>
.wrapper{}
</style>